.give-square-connected {
  color: #227528;
}

.give-square-not-connected {
  .give-field-description {
	span {
	  color: #FF0000;
	}
  }
}

.give-square-btn {
  background-color: #383F49;
  display: inline-block;
  padding: 8px 12px;
  margin: 0;
  border-radius: 5px;
  font-size: 14px;
  color: #fff;
  text-decoration: none;

  &:hover {
	color: #fff;
  }

  &:focus {
	color: #fff;
  }

  &.give-square-sandbox-btn {
	background-color: #ffffff;
	color: #383f49;
	border: 2px solid #383f49;
  }

  span {
	margin: 0 0 0 8px;
  }

  img {
	height: 16px;
	width: 16px;
	margin: 0 0;
	position: relative;
	top: 2px;
  }
}

/* Square Connect Banner */
#give-square-connect-banner {
	p {
		font-size: 16px;
	}
}



